//! NeuerKunde.......................

function NeuerKunde(props) {
  return (
    <div>
      <div className="ueberschrift1">
        <h1>Neuer Kunde</h1>
      </div>
      <form onSubmit={props.nummerSpeichern}>
        <input
          type="text"
          placeholder="Neue Nummer"
          value={props.valueEingabe}
          onChange={props.nummerEingabe}
          required
          className="inputCSS"
        ></input>
        <button type="submit" className="speichernsuchenButtons">
          Speichern
        </button>
      </form>
      <h2>{props.neueNummerProp}</h2>
    </div>
  );
}
//! Kunde suchen (nach Nummer).......................

function KundeSuchen(props) {
  return (
    <div>
      <h1>Kunde suchen</h1>

      <form onSubmit={props.handleSubmit}>
        <input
          type="text"
          placeholder="Suche Nummer"
          value={props.suchvalue}
          onChange={props.handleChange}
          required
          className="inputCSS"
        ></input>
        <button type="submit" className="speichernsuchenButtons">
          Suchen
        </button>
      </form>
    </div>
  );
}

//! KundenInfo.......................

function KundenInfo(props) {
  return (
    <div>
      <h1>{props.neueNummerProp}</h1>
      <h1>{props.gesuchteNummer}</h1>
      <form onSubmit={props.handleSubmit}>
        <input
          placeholder="Name des Kunden"
          value={props.valueEingabe}
          onChange={props.handleChange}
          className="inputCSS"
        ></input>
        <button type="submit" className="speichernsuchenButtons">
          Speichern
        </button>
      </form>
      <h4>
        Name: {props.nameGespeichert} {props.gespeicherterName}
      </h4>
      <hr></hr>
      <div className="infoCSS">
        <h4>Registriert?</h4>
        <div>
          <button
            id="buttonEr"
            onClick={props.klickRegEr}
            className="infoButton linkerButton"
          >
            Er
          </button>
          <button
            id="buttonWir"
            onClick={props.klickRegWir}
            className="infoButton"
          >
            Wir
          </button>
        </div>
        <div id="anrufDIV" className={props.toggleVersteckt}>
          <h4>Anruf angenommen</h4>
          <div>
            <button
              id="anrufJa"
              onClick={props.klickAnrufJa}
              className="infoButton linkerButton"
            >
              Ja
            </button>
            <button
              id="anrufNein"
              onClick={props.klickAnrufNein}
              className="infoButton"
            >
              Noch nicht
            </button>
          </div>
        </div>
        <h4>Follow Up gesendet?</h4>
        <button
          id="buttonFollowUp"
          onClick={props.klickFollowUp}
          className="infoButton"
        >
          Follow Up
        </button>
      </div>
      <hr></hr>
      <button
        onClick={props.statusSpeichern}
        className="speichernsuchenButtons"
      >
        SPEICHERN
      </button>
      <hr></hr>
      <hr></hr>
      <button onClick={props.zuruecksetzen} className="zuruecksetzenCSS">
        Zurücksetzen
      </button>
      <hr></hr>
    </div>
  );
}

//// APP..............................

export default function App() {
  let [neueNummer, setNeueNummer] = useState("");
  let [nummerTippen, setNummerTippen] = useState("");
  let [isHiddenSuche, setIsHiddenSuche] = useState(false);

  let [divVersteckt, setDivVersteckt] = useState("versteckt");
  let [kundeNameTippen, setKundeNameTippen] = useState("");
  let [kundeName, setKundeName] = useState("");

  // Ursprünglicher Status für den State eines Kunden
  let standardStatus = {
    kunde: null, // Nummer des Kunden wird hier eingetragen
    name: null, // Kunden Name, falls eingetragen
    registriert: null, // selbst , wir
    anruf: null, // ja, nochNicht
    followUp: null, // ja
  };

  let [kundenStatus, setKundenStatus] = useState(standardStatus);

  //! //////////////////////////////////////////////////////////////
  //! State für Kunde Suchen
  //! //////////////////////////////////////////////////////////////

  let [sucheNummer, setSucheNummer] = useState("");
  let [sucheNummerTippen, setSucheNummerTippen] = useState("");

  function sucheNummerEingabe(event) {
    setSucheNummerTippen(event.target.value);
  }

  function getippteNummerSuchen(event) {
    event.preventDefault();

    !sucheNummerTippen && setSucheNummer(sucheNummerTippen);
    console.log("diese nummer wird gesucht: ", sucheNummerTippen);

    db.collection("kunden")
      .doc(sucheNummerTippen)
      .get()
      .then((antwort) => {
        console.log(antwort.data());
        if (antwort.data() == undefined) {
          console.log("undefinnnneed");
          document
            .getElementById("nichtGefundenMeldung")
            .classList.remove("versteckt");
        } else {
          toggleHiddenSuche();
          let kundenStatusFirebase = antwort.data();
          console.log(kundenStatusFirebase);
          kundenStatusFirebase.kunde = sucheNummerTippen;
          setKundenStatus(kundenStatusFirebase);

          // Farbe der Buttons anpassen:

          if (kundenStatusFirebase.anruf == "ja") {
            document.getElementById("anrufJa").classList.add("geklickt");
          }
          if (kundenStatusFirebase.anruf == "nein") {
            document.getElementById("anrufNein").classList.add("geklickt");
          }
          if (kundenStatusFirebase.registriert == "er") {
            document.getElementById("buttonEr").classList.add("geklickt");
          }
          if (kundenStatusFirebase.registriert == "wir") {
            document.getElementById("buttonWir").classList.add("geklickt");
          }
          if (kundenStatusFirebase.followUp == "ja") {
            document.getElementById("buttonFollowUp").classList.add("geklickt");
          }
          if (kundenStatusFirebase.registriert == "er" || "wir") {
            setDivVersteckt("");
          }
        }
      });
  }

  //! //////////////////////////////////////////////////////////////
  //! ENDE :  State für Kunde Suchen
  //! //////////////////////////////////////////////////////////////

  //// NeuerKunde EventHandler

  function nummerSpeichernFunktion(event) {
    // event.target.value in Firebase eintragen

    event.preventDefault();
    // setNeueNummer(nummerTippen);

    toggleHiddenSuche();
    document.getElementById("nichtGefundenMeldung").classList.add("versteckt");

    let tempKundenStatus = { ...kundenStatus };
    tempKundenStatus.kunde = nummerTippen;
    setKundenStatus(tempKundenStatus);
  }

  function nummerEingabeFunktion(event) {
    setNummerTippen(event.target.value);
  }

  //// Hidden Components

  function toggleHiddenSuche() {
    setIsHiddenSuche(!isHiddenSuche);
  }

  //// KundenInfo Edit EventHandler

  function kundeNameEingabe(event) {
    setKundeNameTippen(event.target.value);
  }

  function kundeNameSpeichern(event) {
    event.preventDefault();
    setKundeName(kundeNameTippen);

    let tempKundenStatus = { ...kundenStatus };
    tempKundenStatus.name = kundeNameTippen;
    setKundenStatus(tempKundenStatus);
  }

  //// Buttons Click Handlders

  function registrationEr() {
    setKundenStatus({ ...kundenStatus, registriert: "er" });

    // Toggle Farbe zwischen Er und Wir
    document.getElementById("buttonEr").classList.add("geklickt");
    if (document.getElementById("buttonWir").classList.contains("geklickt")) {
      document.getElementById("buttonWir").classList.remove("geklickt");
    }
    setDivVersteckt("");
  }

  function registrationWir() {
    setKundenStatus({ ...kundenStatus, registriert: "wir" });

    // Toggle Farbe zwischen Er und Wir
    document.getElementById("buttonWir").classList.add("geklickt");
    if (document.getElementById("buttonEr").classList.contains("geklickt")) {
      document.getElementById("buttonEr").classList.remove("geklickt");
    }
    setDivVersteckt("");
  }

  function anrufJa() {
    setKundenStatus({ ...kundenStatus, anruf: "ja" });

    // Toggle Farbe zwischen Er und Wir
    document.getElementById("anrufJa").classList.add("geklickt");
    if (document.getElementById("anrufNein").classList.contains("geklickt")) {
      document.getElementById("anrufNein").classList.remove("geklickt");
    }
  }
  function anrufNein() {
    setKundenStatus({ ...kundenStatus, anruf: "nein" });

    // Toggle Farbe zwischen Er und Wir
    document.getElementById("anrufNein").classList.add("geklickt");
    if (document.getElementById("anrufJa").classList.contains("geklickt")) {
      document.getElementById("anrufJa").classList.remove("geklickt");
    }
  }

  function followUpFkt() {
    setKundenStatus({ ...kundenStatus, followUp: "ja" });
    document.getElementById("buttonFollowUp").classList.add("geklickt");
  }

  //// Status zurücksetzen

  function zuruecksetzenStatus() {
    let nameDesKunden = kundenStatus.name;
    setKundenStatus({ ...standardStatus, name: nameDesKunden });

    // Button Farben zuruecksetzen
    document.getElementById("anrufJa").classList.remove("geklickt");
    document.getElementById("anrufNein").classList.remove("geklickt");
    document.getElementById("buttonFollowUp").classList.remove("geklickt");
    document.getElementById("buttonEr").classList.remove("geklickt");
    document.getElementById("buttonWir").classList.remove("geklickt");
    // Anruf Div wieder verstecken
    setDivVersteckt("versteckt");
  }

  //// FIREBASE speichern

  function speichernFirebase() {
    db.collection("kunden").doc(kundenStatus.kunde).set(kundenStatus);
  }

  //// Return Render

  return (
    <div className="bodyHaupt">
      <div>
        {!isHiddenSuche && (
          <NeuerKunde
            nummerEingabe={(event) => nummerEingabeFunktion(event)}
            nummerSpeichern={(event) => nummerSpeichernFunktion(event)}
            valueEingabe={nummerTippen}
            neueNummerProp={neueNummer}
          />
        )}
        <hr></hr>
        {!isHiddenSuche && (
          <KundeSuchen
            suchvalue={sucheNummerTippen}
            handleChange={(event) => sucheNummerEingabe(event)}
            handleSubmit={(event) => getippteNummerSuchen(event)}
          />
        )}
      </div>
      <div>
        {isHiddenSuche && (
          <KundenInfo
            neueNummerProp={neueNummer}
            valueEingabe={kundeNameTippen}
            handleChange={(event) => kundeNameEingabe(event)}
            handleSubmit={(event) => kundeNameSpeichern(event)}
            nameGespeichert={kundeName}
            klickRegEr={() => registrationEr()}
            klickRegWir={() => registrationWir()}
            klickAnrufJa={() => anrufJa()}
            klickAnrufNein={() => anrufNein()}
            klickFollowUp={() => followUpFkt()}
            zuruecksetzen={() => zuruecksetzenStatus()}
            toggleVersteckt={divVersteckt}
            statusSpeichern={() => speichernFirebase()}
            gesuchteNummer={kundenStatus.kunde}
            gespeicherterName={kundenStatus.name}
          />
        )}
      </div>
      <p id="nichtGefundenMeldung" className="nixgefunden versteckt">
        Keinen solchen Kunden gefunden."
      </p>
    </div>
  );
}
